#qrcode {
  width: 200px;
  height: 200px;
}

#homepage {
  width: 100%;
  height: 100%;
}

#introme {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#name {
  font-size: 32px;
  margin-top: 50px;
  margin-bottom: 20px;
}

#about {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 220px;
}

#bio {
  /* width: 80%; */
  display: flex;
  align-items: center;
  justify-content: center;
}

#descript {
  width: 40%;
  margin-left: 60px;
}
.icon {
  color: #4d4d4d;
  font-size: 75px;
}
.icon:hover {
  transition: 3s;
  transform: rotateY(360deg);
}

.type {
  display: flex;
}

#skills {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 220px;
}

#skillItems {
  display: flex;
  justify-content: center;
  width: 50%;
  flex-wrap: wrap;
}

.iconItem {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-right: 40px;
}

#me {
  width: 250px;
  border-radius: 500px;
  object-position: left;
  /* transform: scale(1.5); */
}

a {
  text-decoration: none;
}

.personal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 0.5s;
}

#projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#projectItems {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  /* height: 1000px; */
  justify-content: center;
}

.projectItem {
  background-color: #4d4d4d;
  margin: 20px;
  width: 300px;
  height: 150px;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  padding: 10px;
}

.projLogo {
  width: 200px;
}

.projectItem:hover {
  width: 500px;
  height: 400px;
  transition: 1s;
}

.projDes {
  width: 300px;
  display: none;
  transition: 2s;
}

.projectItem:hover > .projDes {
  display: flex;

  /* transition: 2s; */
}
.projectItem:hover > .projInfo {
  /* width: 80%; */
  display: flex;

  /* transition: 2s; */
}

.personal:hover {
  transition: 0.5s;
  transform: scale(1.2);
}

.projInfo {
  color: white;
  display: none;
}

a {
  text-decoration: none;
}
