.navBar {
  /* position: fixed; */
  margin: 0px;
  padding: 0px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 40px;
}
nav {
  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%; /* Full width */
  background-color: #4d4d4d;
  color: white;
}

.navItem {
  font-size: 14px;
  margin-right: 20px;
  cursor: pointer;
}

#logo {
  position: absolute;
  width: 100px;
  top: -40px;
  padding-left: 20px;
}

.navItem:hover {
  transition: 0.2s;
  padding-bottom: 2px;
  border-bottom: 3px solid white;
}

#email {
  color: white;
}
